import React, { useState, useEffect} from 'react';
import * as Realm from 'realm-web';
import Form from '@rjsf/core';
import validator from '@rjsf/validator-ajv8';
import Home from './page';



const app = new Realm.App({ id: process.env.REACT_APP_REALM_ID });
const user = app.currentUser;

const schema = {
  title: 'Register',
  type: 'object',
  required: ['email', 'password'],
  properties: {
    email: { type: 'string', title: 'Email', format: 'email' },
    password: { type: 'string', title: 'Password', minLength: 6, format: 'password' },
  },
};

const loginSchema = {
  title: 'Login',
  type: 'object',
  required: ['email', 'password'],
  properties: {
    email: { type: 'string', title: 'Email', format: 'email' },
    password: { type: 'string', title: 'Password', minLength: 6, format: 'password' },
  },
};

const Logout = () => {

  const [showRegisterForm, setShowRegisterForm] = useState(false);
  const [loading, setLoading] = useState(true);




  useEffect(() => {
    const fetchUser = async () => {
        try {
          // Authenticate the user
          if (user) { // Kiểm tra xem user có tồn tại không trước khi gọi fetchUser
            logout();
          }
        } catch (error) {
          console.log(error.error);
        }
    };

    if (user) {
        fetchUser();
      }
      
  }, []); // Thêm logOut vào danh sách dependency của useEffect

  const logout = async () => {
    if (user) {
      try {
        await user.logOut();
        window.location.reload(true)
      } catch (error) {
        console.error('Error logging out:', error);
      }
    }
  };
//Register
const register = async (form) =>{

  const {email, password} = form?.formData

  try {

    await app.emailPasswordAuth.registerUser({email, password});
    window.location.reload(true)

  } catch (error) {
    console.log(error.error)
  }
}


  const login = async (form) =>{

    const {email, password} = form?.formData

    try {
      // Create an API Key credential
      const credentials = Realm.Credentials.emailPassword(email, password);
      // Authenticate the user
      await app.logIn(credentials);
      setLoading(false);
      window.location.href = '/app-test';
  

    } catch (error) {
      console.log(error.error)

    }
}

  return (
    <div>
      {user ? (
        <>
          {loading ? <p>Loading...</p> : <Home />}
        </>
      ) : (
        <div className="overlay-container">
          <div className="overlay-content">
            <div className="container_form">
              {!showRegisterForm ? (
                <>
                  <button className="button1" onClick={() => setShowRegisterForm(true)}>
                    Đăng ký
                  </button>
                  <Form
                    className="custom-form"
                    schema={loginSchema}
                    validator={validator}
                    onSubmit={login}
                  />
                </>
              ) : (
                <>
                  <button className="button1" onClick={() => setShowRegisterForm(false)}>
                    Quay lại
                  </button>
                  <Form
                    className="custom-form"
                    schema={schema}
                    validator={validator}
                    onSubmit={register}
                  />
                </>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Logout;
